<template>
  <div
    class="Container-TssignTask"
    v-if="['ROOT', 'ADMIN', 'CREATOR'].includes(userRole)"
  >
    <div style="padding-bottom:20px;width:100%;">
      <!-- 面包屑 -->
      <el-breadcrumb class="Breadcrumb">
        <el-breadcrumb-item class="pathActive">设备保养</el-breadcrumb-item>
        <el-breadcrumb-item class="active">任务分配</el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <div class="chuansuo">
      <div class="kuang">
        <el-transfer
          filterable
          v-model="value1"
          :data="data1"
          :titles="titles1"
          @change="change1"
        >
        </el-transfer>
      </div>
      <div class="kuang kuang2">
        <el-transfer
          filterable
          v-model="value2"
          :data="data2"
          :titles="titles2"
          @change="change2"
        >
        </el-transfer>
      </div>
      <div class="kuang kuang3">
        <el-transfer
          filterable
          v-model="value3"
          :data="data3"
          :titles="titles3"
          @change="change3"
        >
        </el-transfer>
      </div>
      <div class="Btns">
        <div class="sub-btn">
          <el-button @click="SubTssign">保存</el-button>
        </div>
        <div class="del-btn">
          <el-button>删除</el-button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";
export default {
  name: "AddTaskInside",
  created: function() {
    let that = this;
    axios.get("http://47.102.214.37:8080/user/me").then((res) => {
      console.log(res.data);
      that.userRole = res.data.role;
      that.userID = res.data.id;
    });
    setTimeout(function() {
      if (["ROOT", "ADMIN", "CREATOR"].includes(that.userRole)) {
        // 获取全部任务
        axios
          .get("http://47.102.214.37:8080/ops/schedule?page=0&size=100")
          .then((res) => {
            for (var i = 0; i < res.data.content.length; i++) {
              let obj = {};
              // console.log(res.data.content[i]);
              obj.key = res.data.content[i].id;
              obj.label = res.data.content[i].name;
              that.data1.unshift(obj);
            }
          });
        // 获取全部 OPERATOR 员工
        axios
          .get("http://47.102.214.37:8080/user/query?role==OPERATOR")
          .then((res) => {
            for (var i = 0; i < res.data.content.length; i++) {
              // console.log(res.data.content[i]);
              let obj = {};
              obj.key = res.data.content[i].id;
              obj.label = res.data.content[i].username;
              that.data2.unshift(obj);
            }
          });
        // 获取全部设备
        axios
          .get("http://47.102.214.37:8080/device/query?name=!")
          .then((res) => {
            console.log(res.data);
            for (var i = 0; i < res.data.content.length; i++) {
              // console.log(res.data.content[i]);
              let obj = {};
              obj.key = res.data.content[i].id;
              obj.label = res.data.content[i].name;
              that.data3.push(obj);
            }
          });
      }
    }, 200);
  },
  data() {
    return {
      userRole: "", //用户类型
      // 选择框 1
      data1: [],
      value1: [],
      titles1: ["任务名称", "已选择任务"],
      // 选择框 2
      data2: [],
      value2: [],
      titles2: ["人员列表", "已选择人员"],
      // 选择框 3
      data3: [],
      value3: [],
      titles3: ["设备列表", "已选择设备"],
      selectedTaskid: [],
      selectedUserid: [],
      selectedDeviceid: [],
    };
  },
  methods: {
    change1(res) {
      let that = this;
      console.log(res);
      for (var i = 0; i < res.length; i++) {
        that.selectedTaskid.push(res[i]);
      }
    },
    change2(res) {
      let that = this;
      console.log(res);
      for (var i = 0; i < res.length; i++) {
        that.selectedUserid.push(res[i]);
      }
    },
    change3(res) {
      let that = this;
      console.log(res);
      for (var i = 0; i < res.length; i++) {
        that.selectedDeviceid.push(res[i]);
      }
    },
    SubTssign() {
      let that = this;
      console.log(that.selectedTaskid);
      console.log(that.selectedUserid);
      if (that.selectedTaskid.length == 0) {
        that.$message({
          message: "请将信息填写完整",
          type: "warning",
        });
      } else {
        console.log(that.selectedUserid);
        console.log(that.selectedDeviceid);
        let opsid = [];
        let deviceid = [];
        for (var p = 0; p < that.selectedUserid.length; p++) {
          opsid.push({ id: that.selectedUserid[p] });
        }
        for (var q = 0; q < that.selectedDeviceid.length; q++) {
          deviceid.push({ id: that.selectedDeviceid[q] });
        }
        console.log(opsid);
        console.log(deviceid);
        for (var a = 0; a < that.selectedTaskid.length; a++) {
          let url =
            "http://47.102.214.37:8080/ops/schedule/detail/" +
            that.selectedTaskid[a];
          axios.get(url).then((res) => {
            console.log(res.data);
            let acceptedStandard = res.data.acceptedStandard;
            let content = res.data.content;
            let id = res.data.id;
            let name = res.data.name;
            let parent = res.data.parent;
            let remark = res.data.remark;
            let scheduleDay = res.data.scheduleDay;
            let scheduleType = res.data.scheduleType;
            let side = res.data.side;
            let tools = res.data.tools;
            setTimeout(function() {
              axios
                .put(url, {
                  acceptedStandard: acceptedStandard,
                  content: content,
                  device: deviceid,
                  id: id,
                  name: name,
                  ops: opsid,
                  parent: parent,
                  remark: remark,
                  scheduleDay: scheduleDay,
                  scheduleType: scheduleType,
                  side: side,
                  tools: tools,
                })
                .then((res) => {
                  console.log(res);
                  that.$message({
                    message: "分配成功",
                    type: "success",
                  });
                });
            }, 200);
          });
        }
      }
    },
  },
};
</script>

